import React from 'react';
import './weather.css';
import wind from './assets/wind_black.png';
import sun from './assets/sun_black.png';
import wet from './assets/humidity_black.png';
import moon from './assets/moon_black.png';
import no_net from './assets/no_net.png';


class App extends React.Component{

  constructor(props){

    super(props)

    this.interval = undefined;
    this.state = {
      gotAll:undefined,
      temperature : undefined,
      temp_max: undefined,
      temp_min: undefined,
      wind : undefined,
      humidity: undefined,
      weather_condition:undefined,
      sun_moon : undefined,
      day_night : undefined,
      date: undefined,
      day : undefined,
      hours : undefined,
      minutes: undefined
    };

    this.days = ['Sunday','Monday','Tuesday','Wednesday','Thursday','Friday','Saturday'];
    this.months = ['January','February','March','April','May','June','July','August','September','October','November','December'];

  }

  async getWeather(latitude,longitude){
    var timestamp = Date.now()/1000;
    var d = new Date();
    var url= 'https://api.openweathermap.org/data/2.5/weather?lat=' + String(latitude) + '&lon='+ String(longitude) + '&units=metric&APPID=83c6ba4dd07d83514536821a8a51d6d5';
    const response  =  await fetch(url);
    const jsons = await response.json();
    // console.log(jsons);
    this.setState({temperature:jsons.main.temp});
    this.setState({temp_max:jsons.main.temp_max});
    this.setState({temp_min:jsons.main.temp_min});
    this.setState({wind:jsons.wind.speed});
    this.setState({humidity:jsons.main.humidity});
    this.setState({weather_condition:jsons.weather.main});
    if(timestamp > jsons.sys.sunrise && timestamp < jsons.sys.sunset){
      this.setState({sun_moon:sun});
      this.setState({day_night:"Day"});
    }
    else{
      this.setState({day_night:"Night"});
      this.setState({sun_moon:moon});
    }

    this.setState({day:this.days[d.getDay()]});
    this.setState({date:String(d.getDate()) + " " + this.months[d.getMonth()] + " " + String(d.getUTCFullYear())});
    this.setState({gotAll:true});
    // console.log(this.state);

  }

  getPosition = () => {
    return new Promise(function (resolve, reject) {
      navigator.geolocation.getCurrentPosition(resolve, reject);
    });
  }

  componentDidMount(){
    // console.log();
 
    this.getPosition().then(response =>{
      this.getWeather(response.coords.latitude,response.coords.longitude)
    })
    .catch((err) => {
      this.setState({gotAll:false});
    });

    this.interval = setInterval(() => {
      var d =  new Date();
      this.setState({hours:d.getHours()});
      this.setState({minutes:d.getMinutes()});
      // console.log(this.state);
    }, 1000);

  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }




  render(){
    if(this.state.gotAll === true){
      return (

        <div style={{color:"black"}}>
          
          <div>
            <div  style={{float:"left",paddingRight:"100px"}}>
              <img src= {wind} alt="wind" style={{height: "30px",width: "30px", marginBottom:"-8px",paddingRight:"5px"}} />
              <label className="wind_header">{this.state.wind} km/hr</label>
            </div>
            <label className="wind_header">{this.state.day}, </label>
            <label className="wind_header">{this.state.date}</label>
            {/* <div>
  
            </div> */}
            
            <div style={{float:"right"}}>
              <img src= {wet} alt="wind" style={{height: "25px",width: "25px", marginBottom:"-4px",paddingLeft:"15px",paddingRight:"7px"}} />
              <label className="wind_header">{this.state.humidity}%</label>
            </div>
         
          </div>  
          <br/><br/>
          
  
          
          <br/>
  
          <div style={{float:"left"}}>
            <label className="main_header">{this.state.hours}</label>
            <label className="blink">:</label>
            <label className="main_header">{this.state.minutes}</label>
          </div>
          <div style={{float:"right",marginRight:"50px"}}>
            <img src={this.state.sun_moon} alt="wind" id="day_night" style={{height: "70px",width: "70px", marginBottom:"-10px",paddingRight:"15px"}}/>
            <label className="main_header">{this.state.temperature}°C</label> <br/>
            <label className="wind_header" style={{float:"right",fontSize:"25px"}}>{this.state.day_night} </label>
          </div>
          {/* <header className="wind_header" style={{fontSize:"22px"}}>{this.state.temp_max}°C (max)</header>
          <div className="weather_bar"></div>
          <header className="wind_header" style={{fontSize:"22px"}}>{this.state.temp_min}°C(min)</header> */}
        </div>
  
      );
    }
    else{
      return(
        <div>
          <img src={no_net} alt="no_net" style={{height: "40px",width: "40px", marginBottom:"-10px",paddingRight:"15px"}}/>
          <label className="main_header" style={{fontSize:"20px",marginTop:"-10px",color:"red"}}>Please Check your internet connection</label> 
        </div>
      );

    }

  }

}

export default App;
